.content-reviews {
  // .content-reviews__items

  &__items {
    margin: 50px 0 30px;
  }
}

.btn-reviews {
  font-family: var(--font-family);
  font-size: clamp(2.1rem, 2.726rem - 3.13vw, 0.14375rem);
  border-radius: var(--br);
  padding: 20px;
  color: var(--dark-color-06);
  transition: all .3s ease-in-out;

  &.active {
    background-color: var(--grey-color-07);
    color: var(--dark-color);
  }

}

.parent-text {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 180px;

  &__item {
    grid-column: span 2;
    grid-row: span 2;

    &:first-child {
      grid-column: span 2;
      grid-row: span 2;
    }

    &:nth-child(7n+2) {
      grid-column: span 1;
      grid-row: span 3;
    }

    &:nth-child(7n+3) {
      grid-column: span 1;
      grid-row: span 2;
    }

    &:nth-child(7n+4) {
      grid-column: span 1;
      grid-row: span 3;
    }

    &:nth-child(7n+5) {
      grid-column: span 1;
      grid-row: span 3;
    }

    &:nth-child(7n+6) {
      grid-column: span 1;
      grid-row: span 3;
    }

    &:nth-child(7n+7) {
      grid-column: span 1;
      grid-row: span 2;
    }

    &:nth-child(-7n+8) {
      grid-column: span 2;
      grid-row: span 2;
    }
  }
}


.parent-text-img {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 180px;

  &__item {
    grid-column: span 2;
    grid-row: span 2;

    &:first-child {
      grid-column: span 2;
      grid-row: span 2;
    }

    &:nth-child(4n+2) {
      grid-column: span 2;
      grid-row: span 3;
    }

    &:nth-child(4n+3) {
      grid-column: span 2;
      grid-row: span 3;

    }

    &:nth-child(4n+4) {
      grid-column: span 2;
      grid-row: span 2;
    }

    &:nth-child(4n+2),
    &:nth-child(4n+3) {

      .parent-item__header {
        position: absolute;
        width: calc(50% - (var(--padding)*2));
        height: 100%;
        display: block;
        left: 0;
        top: 0;

        @include m-560 {
          position: relative;
          display: flex;
          align-items: center;
          width: 100%;
        }

        .parent-item__img {
          width: 100%;
          height: 100%;
          border-radius: 16px;

          @include m-560 {
            flex: 0 0 70px;
            width: 70px;
            height: 70px;
            border-radius: 50%;
            overflow: hidden;
          }
        }

        .parent-item__desc {
          position: absolute;
          left: 30px;
          bottom: 20px;

          @include m-560 {
            position: relative;
            left: 0;
            bottom: 0;
            width: 100%;
          }

          h3 {
            margin-left: 0px;

            @include m-560 {
              margin-left: 14px;
            }
          }
        }


      }

      .parent-item__body,
      .parent-item__footer {
        width: 50%;
        margin-left: auto;

        @include m-560 {
          width: 100%;
          margin-left: 0;
        }
      }
    }

  }
}


.items-review {

  // .items-review__parent

  &__parent {
    @include m-960 {
      display: flex;
      overflow-x: auto;
      padding: 20px 0 40px;
    }
  }
}


.parent {
  --padding: 1rem;
  grid-column-gap: 20px;
  grid-row-gap: 20px;

  &-item {
    border-radius: var(--br);
    position: relative;
    background-color: var(--grey-color);
    min-height: 180px;
    padding: var(--padding);
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    font-family: var(--font-family);
    letter-spacing: .01rem;

    @include m-960 {
      width: 500px;
      flex: 0 0 500px;
      max-height: 360px;
    }

    @include m-560 {
      width: 400px;
      flex: 0 0 400px;
      max-height: 460px;
    }

    @include m-460 {
      width: 290px;
      flex: 0 0 290px;
    }

    &__header {
      display: flex;
      align-items: center;
    }

    &__img {
      flex: 0 0 70px;
      width: 70px;
      height: 70px;
      border-radius: 50%;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__title {
      position: relative;
      z-index: 1;

      h3 {
        display: inline-block;
        background-color: var(--white-color);
        border: 2px solid var(--black-color);
        padding: 4px 10px;
        border-radius: 100px;
        font-size: clamp(0.7rem, 0.5870rem + 0.3478vw, 0.9rem);
        margin-left: 14px;
      }
    }

    &__grage {
      color: var(--white-color);
      display: inline-block;
      background-color: var(--black-color);
      border: 2px solid var(--black-color);
      // padding: 4px 10px;
      padding: 20px 10px 5px;
      border-radius: 100px;
      font-size: clamp(0.7rem, 0.587rem + 0.3478vw, 0.9rem);
      font-family: var(--font-family);
      position: relative;
      z-index: 0;
      // transform: rotate(-4deg) translate(20px, -7px);
      transform: translate(20px, -15px);
      letter-spacing: 1px;
      line-height: 100%;
    }

    &__body {
      margin: 0;
      position: relative;
      overflow: hidden;
      margin-top: 20px;
      flex: 1;
      height: 100%;

      &:before {
        content: '';
        position: absolute;
        bottom: 0px;
        left: 0px;
        width: 100%;
        height: 100px;
        background-image: linear-gradient(to bottom, transparent 0%, var(--grey-color) 100%);
        user-select: none;
      }

      p {
        margin: 0 0 0.5rem;
        font-family: var(--font-family);
        line-height: 154%;
      }
    }

    &__footer {
      margin: 20px 0 10px;
    }

    &:first-child {
      background-color: var(--white-color);
      border: 3px solid var(--black-color);

      .parent-item__img {
        flex: 0 0 100px;
        width: 100px;
        height: 100px;
      }

      .parent-item__header {
        position: relative;
        top: -34px;
      }

      .parent-item__desc {
        margin: 30px 0 0;
      }

      .parent-item__body {
        position: relative;
        top: -34px;
        // max-height: 220px;

        &:before {
          background-image: linear-gradient(to bottom, transparent 0%, var(--white-color) 100%);
        }

        p {
          font-size: clamp(1.1rem, 1.0296rem + 0.3380vw, 1.3rem);
          line-height: 130%;
        }
      }

      .parent-item__footer {
        margin: 0 0 10px;
      }
    }

  }
}